.advantages {
    background-color: rgba(132, 161, 120, 1);
    padding-top: 50px;
    padding-bottom: 64px;

    @media screen and (min-width: 480px) {
        width: 100%;
    }
}

.advantages__title {
    display: none;
}

.advantages__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;


    @media screen and (min-width:768px) {
        flex-direction: row;
        justify-content: space-around;

    }
}

.advantages__item {
    width: 100%;
    position: relative;
    

    &:before {
        content: '';
        width: 50px;
        height: 50px;
        background-color: rgba(248, 210, 78, 1);
        border-radius: 50%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 16px;
        display: block;
        background-repeat: no-repeat;
        background-position: center;
    }

    &.first::before {
        background-image: url(../images/advantages/cow-min.png) ;
    }

    &.second::before {
        background-image: url(../images/advantages/weight-min.png);
    }

    &.third::before{
        background-image: url(../images/advantages/icecream-min.png);
    }

    
    &:not(:last-child) {
        margin-bottom: 32px;
    }
    

    @media screen and (min-width:768px) {
        width: 152px;
    }

    @media screen and (min-width: 1200px) {
        width: 368px;
    }
}

.advantages__value {
    font-family: Montserrat;
    font-size: 36px;
    font-weight: 700;
    line-height: 44px;
    letter-spacing: 0.04em;
    text-align: center;

    color: white;
    margin-top: 0;
    margin-bottom: 16px;

    @media screen and (min-width: 768px) {
        font-size: 48px;
        line-height: 59px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 58px;
        line-height: 71px;
    }
}

.advantages__description {
    font-family: Montserrat;
    font-size: 12px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: center;

    color: white;
    // width: 230px;

    // @media screen and (min-width: 480px) {
    //     width: 230px;
    //     text-align: center ;
    //     margin-left: auto;
    //     margin-right: auto;
    // }

    @media screen and (min-width: 768px) {
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0em;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        font-weight: 400;
        line-height: 28px;
        letter-spacing: 0.02em;
    }
}



